@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* blue hexcode: #202945
orange hexcode: #ff6900 */

.container {
  display: flexbox;
  flex-direction: column;
}

/* ------ header section ------ */
.heading {
  display: flex;
  align-items: center;
  height: 90vh;
  width: 100vw;
  background-image: url(/bullhorn/static/media/osman-rana2.d0576126.png); /* this is actually an image of Boston */
  background-repeat: no-repeat;
  background-size: cover;
}

.heading-text {
  display: flex;
  flex-direction: column;
  width: 45vw;
  margin-left: 10vw;
}

.title {
  margin-top: 15vh;
  font-size: 54px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: bolder;
  color: white;
}

.subtitle {
  margin-top: 5vh;
  font-size: 25px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: medium;
  color: white;
}

/* ------ first section ------ */
.section-wrapper {
  display: flex;
  height: 60vh;
  width: 100vw;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.section-text-1 {
  width: 40vw;
}

.title-1 {
  font-size: 25px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: bolder;
  width: 30vw;
}

.subtitle-1 {
  margin-top: 3vh;
  font-family: 'Noto Sans KR', sans-serif;
  width: 30vw;
}

.img-group {
  width: 25vw;
  margin-top: 7vh;
}

/* ------ second section ------ */
.section-wrapper-2 {
  display: flex;
  height: 60vh;
  width: 100vw;
  flex-direction: row;
  justify-content: space-evenly;
  background-color: #202945;
}

.column-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 20vw;
}

.title-2 {
  font-size: 25px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: bolder;
  width: 30vw;
  color: white;
  text-align: center;
}

.subtitle-2 {
  font-family: 'Noto Sans KR', sans-serif;
  color: white;
  text-align: center;
  margin-top: 3vh;
}

.bullhorn-icon {
  margin-top: 5vh;
}
/* ------ third section ------ */
.section-wrapper-3 {
  display: flex;
  flex-direction: column;
  height: 70vh;
}

.categories {
  text-decoration: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 10vh;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 17px;
  font-weight: bold;
}

.title {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 17px;
  font-weight: bold;
  text-decoration: none;
}

.section-details {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.img-innovation {
  width: 35vw;
  margin-top: 7vh;
  padding-left: 10vw;
}

.dots-wrapper {
  display: flex;
  justify-content: center;
}

.dots {
  margin-top: 5vh;
  width: 15vw;
}
/* ------ fourth section ------ */
.section-wrapper-4 {
  display: flex;
  flex-direction: row;
  height: 60vh;
  justify-content: space-evenly;
  margin-top: 5vh;
}

.section-text-2 {
  display: flex;
  flex-direction: column;
  width: 30vw;
  margin-top: 8vh;
}

.section-text-3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.title-3 {
  font-size: 25px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: bolder;
  width: 30vw;
  margin-left: 5vw;
}

.subtitle-3 {
  margin-top: 3vh;
  margin-bottom: 3vh;
  margin-left: 5vw;
}

.list {
  width: 24vw;
  margin-left: 3vw;
}
