.img-work {
  margin-top: 6vh;
  width: 35vw;
}
/* ------ footer section ------ */
.footer-wrapper {
  display: flex;
  flex-direction: row;
  height: 50vh;
  background-color: #202945;
  justify-content: center;
  align-items: center;
}

.button-1 {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 20px;
  color: white;
  border-radius: 3px;
  border-color: white;
  border-width: 2px;
  background-color: transparent;
  margin-right: 2vw;
  height: 50px;
  width: 300px;
}

.button-2 {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 20px;
  color: white;
  border: transparent;
  border-radius: 3px;
  background-color: #ff6900;
  margin-left: 2vw;
  height: 50px;
  width: 300px;
}
.navbar-wrapper {
    display: flex;
    flex-direction: row;
    height: 10vh;
    width: 100vw;
    justify-content: space-between;
}

.bullhorn-logo {
    width: 10vw;
    margin-left: 8vw;
}

.menu-wrapper {
    display: flex;
    flex-direction: row;
    margin-right: 5vw;
    align-items: center;
    justify-content: space-evenly;
    width: 40vw;
}

.menu {
    height: 0vh;
    background-color: white;
    width: 8vw;
}
.menu-item {
    display: flex;
    flex-direction: column;
    background-color: white;
    border: none;
    font-size: 12px;
}

.dropdown-button {
    display: flex;
    flex-direction: column;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 15px;
    border-color: transparent;
    background-color: transparent;
}
